* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.buttons {
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #212121;
  background-color: #c5c5e6;
  border-color: #9595d6;
  padding: 20px 50px;
  font-size: 20px;
}

.btn:hover, .btn:focus {
  background-color: rgba(197, 197, 230, .4);
  border-color: rgba(197, 197, 230, .4);
}

.box-date {
  text-align: center;
}

.timer {
  text-align: center;
  justify-content: center;
  gap: 10px;
  margin: 20px auto;
  font-size: 20px;
  display: flex;
}

.field {
  color: #7239a7;
  flex-direction: column;
  display: flex;
}

input {
  height: 30px;
  background-color: #c698f2;
  border: none;
  border-radius: 30px;
  margin-right: 10px;
}

.btn-date {
  height: 30px;
  width: 60px;
  cursor: pointer;
  background-color: #c698f2;
  border: none;
  border-radius: 30px;
  font-size: 14px;
  transition: background-color .3s linear;
}

.btn-date:focus, .btn-date:hover {
  background-color: #9338e8;
}

